.container{
    margin-bottom: 3rem;
    margin-top: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container-title{
    font-size: 50px;
    margin-bottom: 45px;
    margin-top: 20px;
}

.event--img{
    max-width: 400px;
    height: auto;
    border-radius: 5px;
}

.img-container{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    max-width: 95%;
    margin-bottom: 30px;
}

.img-container>p{
    max-width: 60%;
}

.text-team{
    max-width: 85%;
    background: white;
    border-radius: 10px;
    text-align: justify;
    font-family: cursive;
    font-size: 22px;
    padding: 20px;
}

.vid-container{
    display: flex;
    flex-direction: row;
    margin-top: 80px;
}

.le-team{
    font-size: 23px;        
}

.le-team::before{
    font-size: 32px;        
}

@media (max-width: 1000px) {

    .container-title{
        font-size: 40px;
        margin-bottom: 45px;
        margin-top: 20px;
    }
    
    .vid-container{
        flex-direction: column;
        margin-top: 60px;
        width: 50%;
    }

    .react-player{
        max-height: 400px;
    }
    
    .container{
        margin-bottom: 20px;
    }

    .img-container{
        max-width: 95%;
        justify-content: space-between;
        flex-direction: column;
        gap:20px;
    }

    .event--img{
        max-width: 75%;
        /* height: 130px; */
    }

    .img-container>p{
        max-width: 85%;
    }
    
    .text-team{
        font-size: 20px;
        padding: 7px;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }
    
    .le-team{
        font-size: 23px;        
    }

    .le-team::before{
        font-size: 23px;        
    }
}

@media (max-width: 600px){
    
    .container-title{
        font-size: 35px;
        margin-bottom: 45px;
        margin-top: 20px;
    }

    .vid-container{
        flex-direction: column;
        margin-top: 60px;
        width: 85%;
    }

    .text-team{
        font-size: 16px;
        padding: 12px;
    }
    
    .le-team{
        font-size: 18px;        
    }
    
    .le-team::before{
        font-size: 19px;        
    }
}