.img-container-cont{
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 400px;
}

.contact-title{
    font-size: 50px;
    margin-bottom: 45px;
    margin-top: 20px;
}


.contact--info{
    display: flex;
    justify-content: center;
    flex-direction: column  ;
    align-items: center;
    background: white;
    max-width: 600px;
    padding: 35px;
    border-radius: 10px;
    text-align: center;
    font-size: large;
    font-family: cursive;
    gap:10px

}

.contact--container{
    display: flex;
    justify-content: space-between;
    flex-direction: row  ;
    align-items: center;
    margin-top: 40px;
    margin-left: auto;
    margin-right: auto;
    max-width: 85%;
}

.vid-container-contact{
    flex-direction: column;
    margin-top: 60px;
}

@media (max-width: 1000px) {
    
    .contact-title{
        font-size: 40px;
        margin-bottom: 45px;
        margin-top: 20px;
    }

    .react-player{
        max-height: 300px;
    }
    
    .container{
        margin-bottom: 20px;
    }

    .contact--container{
        max-width: 95%;
        justify-content: space-evenly;
    }

    .event--img{
        max-width: auto;
        height: auto;
    }

    .img-container>p{
        max-width: 55%;
    }
    
    .contact--info{
        font-size: 12px;
        padding: 7px;
        max-width: 55%;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }
    
    .le-team{
        font-size: 15px;        
    }

    .le-team::before{
        font-size: 17px;        
    }
}

@media (max-width: 600px) {

    .contact--container{
        flex-direction: column;
        gap: 30px;
    }

}